<template>
  <div class="home">
    <div class="home_top">
        <div class="nav-bg"></div>
      <div class="home-top">
        <div class="home-top-left">
          <div class="home-title">{{timeText}},<span v-if="isopenname">{{userInfo.nickName}}</span><span v-else @click="gologin">先登录吧</span></div>
        </div>
        <div class="home-top-right">
          <van-icon
            @click="issearchopen"
            size="20px"
            color="#0C34BA"
            class="home-top-icon"
            name="search"
          />
          <van-popup
            v-model="searchopen"
            position="top"
            closeable
            close-icon-position="top-right"
            :style="{ height: '70%' }"
            class="searc-show"
          >
            <div class="searc-div">
              <van-search @input="getSearch"  v-model="searchValue" placeholder="请输入搜索关键词" class="searc"/>
            </div>
            <!-- v-for="( item , index ) in searchData" :key="index" -->
            <div class="searc-box">
              <div class="searc-item " :class=" index % 2 == 0 ? 'p-right' : 'p-left'" v-for="( item , index ) in searchData" :key="index" @click="viewProductInfo(item.pid)">
                <div class="searc-img">
                  <img
                    :src="item.smallImg"
                  />
                </div>
                <div class="searc-text">
                  <div class="searc-name">{{item.name}}</div>
                  <div class="searc-enname">{{item.enname}}</div>
                </div>
              </div>

            </div>
          </van-popup>
          <van-icon color="#0C34BA" size="20px" class="home-top-icon" name="scan" />

          <van-icon color="#0C34BA" size="20px" class="home-top-icon" name="calender-o" />
        </div>
      </div>
    </div>
    <div class="banner-bg"></div>
    <div class="banner-box clearfix">
      <van-swipe class="my-swipe" :autoplay="0" indicator-color="white">
        <van-swipe-item v-for="(item , index ) in bannerData" :key="index" @click="viewProductInfo(item.pid)">
          <img class="auto-img" :src="item.bannerImg" alt />
          <div class="banner-title">
            <div class="banner-name">{{item.name}}</div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="home-hot">
      <van-nav-bar>
        <template #left>
          <div class="hot-top-left">热销商品</div>
        </template>
        <template #right>
          <div class="hot-top-right">查看全部({{hotProductslenght}})</div>
        </template>
      </van-nav-bar>

      <van-swipe :loop="false" :width="100" class="home-hotBox">
        <van-swipe-item v-for="(item , index) in hotProducts" :key="index" class="home-hotItem" @click="viewProductInfo(item.pid)" >
          <div class="hot-box">
            <img class="auto-img" :src="item.smallImg" />
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="home-recommend">
      <van-nav-bar>
        <template #left>
          <div class="hot-top-left">新品推荐</div>
        </template>
        <template #right>
          <div class="hot-top-right">查看全部({{newProductslenght}})</div>
        </template>
      </van-nav-bar>
      <div class="recommend-content clearfix">
        <div
          class="recommend-box fl"
          v-for="( item , index ) in newProducts"
          :key="index"
          :class=" index % 2 == 0 ? 'fr' : 'fl'"
          @click="viewProductInfo(item.pid)"
        >
          <div class="recommend-ing">
            <img :src="item.smallImg" alt />
          </div>
          <div class="recommend-text">
            <div class="recommend-name">{{item.name}}</div>
            <div class="recommend-price">￥{{item.price}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/home.css";
import "../../assets/less/home.less";
// 导入JS文件
import { validForm } from "../../assets/js/validForm";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      clearable: false,
      searchopen: false,
      isopenname:false,
      userInfo:{},
      timeText: "",
      searchValue: "",
      hotProductslenght: 0,
      banners: [
        {
          url: "http://www.kangliuyong.com:10002/images/banner/bga1.jpg",
        },
        {
          url: "http://www.kangliuyong.com:10002/images/banner/bga2.jpg",
        },
        {
          url: "http://www.kangliuyong.com:10002/images/banner/bga3.jpg",
        },
        {
          url: "http://www.kangliuyong.com:10002/images/banner/bga4.jpg",
        },
      ],
      bannerData: [],
      hotbanners: [
        {
          name: "摩卡",
          url: "https://www.starbucks.com.cn/images/products/caffe-mocha.jpg",
        },
        {
          name: "榛果风味拿铁",
          url:
            "https://www.starbucks.com.cn/images/products/hazelnut-flavored-latte.jpg",
        },
        {
          name: "焦糖玛奇朵",
          url:
            "https://www.starbucks.com.cn/images/products/caramel-macchiato.jpg",
        },
        {
          name: "卡布奇诺",
          url: "https://www.starbucks.com.cn/images/products/cappuccino.jpg",
        },
        {
          name: "摩卡可可碎片星冰乐",
          url:
            "https://www.starbucks.com.cn/images/products/mocha-java-chip-frappuccino.jpg",
        },
        {
          name: "抹茶星冰乐",
          url:
            "https://www.starbucks.com.cn/images/products/green-tea-frappuccino.jpg",
        },
        {
          name: "焦糖浓缩咖啡星冰乐",
          url:
            "https://www.starbucks.com.cn/images/products/caramel-espresso-frappuccino.jpg",
        },
      ],
      hotProducts: [],
      newProducts: [],
      newProductslenght: 0,
      // 搜索
      searchData: [],
    };
  },
  methods: {
    issearchopen() {
      this.searchopen = !this.searchopen;
    },
    // 登录
    gologin(){
      this.$router.push({name:"Login"});
    },
    //获取banner
    getbannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 
          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    // 获取热门推荐
    getHot() {
      this.$toast.loading({
        message: "加载中...",
        frobidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        // 如果get请求,参数需要放在params,如果是post请求参数需要放在data
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 
          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
            this.hotProductslenght = this.hotProducts.length;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    newProductsData() {
      this.$toast.loading({
        message: "加载中...",
        frobidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        // 如果get请求,参数需要放在params,如果是post请求参数需要放在data
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 
          if (result.data.code == 500) {
            this.newProducts = result.data.result;
            this.newProductslenght = this.newProducts.length;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    //获取时间段
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好";
      } else {
        this.timeText = "深夜了";
      }
    },
    // 搜索框
    getSearch() {
      // 
      this.$toast.loading({
        message: "加载中...",
        frobidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/search",
        // 如果get请求,参数需要放在params,如果是post请求参数需要放在data
        params: {
          appkey: this.appkey,
          name: this.searchValue,
        },
      }).then((result) => {
        this.$toast.clear();
        
        if (result.data.code == "Q001") {
          this.searchData = result.data.result;
        }
      });
    },
    //点击进入详情界面
     //查看商品详情
    viewProductInfo(pid){
      this.$router.push({name : "Detail" , query: {pid}});
    }, 
    //获取用户信息
    getuser(){

      let tokenString = localStorage.getItem('CSTK');
      if( !tokenString ){
        return;
      }

      this.$toast.loading({
        message:'加载中...',
        forbidClick:true,
        duration:0,
        loadingType:'spinner'
      })

      this.axios({
        methods:'GET',
        url:'/findMy',
        params:{
          appkey:this.appkey,
          tokenString
        }
      }).then(result => {
        this.$toast.clear();
        if( result.data.code == 'A001'){
          if (result.data.result.length > 0) {
            this.isopenname = true,
            this.userInfo = result.data.result[0];
          }
          }
      }).catch(err => {
        this.$toast.clear();

      })


    }
  },
  created() {
    // 获取轮播图
    this.getbannerData();
    //加载推荐商品类
    this.getHot();
    // 加载新品推荐
    this.newProductsData();
    // 加载时间提示
    this.getTime();
    //获取用户信息
    this.getuser();
    // 
  },
};
</script>

